import React from "react";
import styled from "styled-components";
const Container = styled.div`
  flex: 1;
  margin: 3px;
  height: 70vh;
  position: relative;
`;

// const Title1 = styled.h1`
//   color: red;
//   margin-bottom: 20px;
// `;
const Image = styled.img`
  width: 100%;
  height: 100%;
  oblect-fit: cover;
`;
const Info = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;
const Title = styled.h1`
  color: ;
  margin-bottom: 20px;
`;

const Button = styled.button`
  border: none;
  padding: 10px;
  margin-bottom: 20px;
  background-color: #084242;
  color: white;
  cursor: pointer;
  font-weight: 600;
`;

const CategoryItem = ({ item }) => {
  return (
    <Container>
      {/* <Title1>Shop By Category</Title1> */}
      <Image src={item.img} />
      <Info>
        <Title>{item.title}</Title>
        <Button>VIEW MORE</Button>
      </Info>
    </Container>
  );
};
export default CategoryItem;
